.C_login {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100dvw;
    height: 100dvh;
    background-color:#5BA9D9;
    background-image: linear-gradient(0deg,#b4c2ce,#0c283a);

}

h1 {
    color: #ffffff;
    font-size: 4rem;
}

.C_form {
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(45deg,#b4c2ce9a,#0c283a44);
    height: 400px;
    width: 370px;
    border-radius: 7px;

}


.inputs {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    margin-top: 30px;
}

.input {
    width: 90%;
    height: 60px;
}

.input input {
    width: 95%;
    height: 30px;
    background-color: #F2F2F2;
    border-radius: 5px;
    padding-left: 5px;
}

button {
    align-self: center;
    width: 150px;
    height: 35px;
    border-radius: 7px;
}
.alert_login{
    display: flex;
    background-color: #ee363669;
    align-items: center;
    justify-content: center;
    color: #a00d0d;
}
.img_login{
    text-align: center;
}