@media screen and (min-width : 1020px) {
  .c_home{
    display: none;
  }
}


.headerContainer{
   position: fixed;
   top:0;
   left: 0;
   padding: 7px;
   width: 100%;
    display: flex;
    background-color: #153042;

    height:8rem;
    flex-direction: column;
    align-items: space-between;

  }
  
  .b_home{
    width: 25px;
    height:25px;
    padding-left: 10px;
    color: #ffffff;

  }
  .b_home:active{
    background-color: #d6d7d85e;
    color: #282c34;
    border-radius: 50%;

  }
  #h1{
    color: #ffffff;
    font-size: 30px; 
    /* padding-left: 10px; */
    margin: 0.5rem;
  }
  .content{
    display:flex;
    width: 100%;
    height: 100%;
    flex-direction: column;
    padding-left: 10px;
    color: #ffffff;
    font-size: 14px;
    gap: 5px;
  }
  .infoProd{
  display: flex;
  gap: 10px;
  }
  .infoAudi{
    display: flex;
    gap: 30px;
  }
  .spanHeard{
      color: rgb(155, 150, 150);
}
 .labelHeard{
    color: #ffffff;
 }