.C_register {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100dvw;
    min-height: 100dvh;
    background-color: #5BA9D9;
    background-image: linear-gradient(0deg, #b4c2ce, #0c283a);

}

.h1_rg {
    color: #ffffff;
    font-size: 3.5rem;
}

.C_form_rg {
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(45deg, #b4c2ce9a, #0c283a44);
    height: 400px;
    width: 370px;
    border-radius: 7px;

}


.inputs_rg {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    margin-top: 30px;
}

.input_rg {
    width: 90%;
    height: 60px;
}

.input_rg input {
    width: 95%;
    height: 30px;
    background-color: #F2F2F2;
    border-radius: 5px;
    padding-left: 5px;
}
.C_button_rg{
    display: flex;
    justify-content: center;
    margin-top: 30px;
}
button {
    align-self: center;
    width: 150px;
    height: 35px;
    border-radius: 7px;
}

.alert_resgister {
    display: flex;
    background-color: #ee363669;
    justify-content: center;
    color: #a00d0d;
    border-radius: 7px;
    padding-left: 10px;
}