.C_register_celula {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100dvw;
    min-height: 100dvh;
    background-color: #5BA9D9;
    background-image: linear-gradient(0deg, #b4c2ce, #0c283a);

}

.msg_celula {
    width: 100%;
    height: 50px;

}

.msg {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 60px;
    background-color: #3aa73d56;
    color: #4CAF50;
}

.C_form_rg_celula {
    display: flex;
    flex-direction: column;
    background-image: linear-gradient(45deg, #b4c2ce9a, #0c283a44);

    width: 80%;
    border-radius: 7px;
    align-items: center;
}


.inputs_rg_celula {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    margin-top: 30px;
}

.inputs_rg_celula {
    width: 90%;
    height: 60px;

}

.C_button_rg {
    display: flex;
    justify-content: center;
    margin: 30px;
}

button {
    align-self: center;
    width: 150px;
    height: 35px;
    border-radius: 7px;
    margin-bottom: 10px;
}

.alert_resgister {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    background-color: #ee363669;
    justify-content: center;
    color: #a00d0d;
    border-radius: 7px;
    padding-left: 10px;
}




.table-responsive {
    overflow-x: auto;
    width: 100%;
}


table {
    width: 95%;
    border-collapse: collapse;
    margin: 20px 0;
    font-size: 18px;
    text-align: left;
    user-select: none;
}

thead {
    background-color: #4cacaf;
    color: white;
}

thead th {
    padding: 12px;
}

tbody tr:nth-child(even) {
    background-color: #e9e0e0;
}

tbody tr:nth-child(odd) {
    background-color: #bed7e0;
}

tbody tr:hover {
    background-color: #ddd;
}

td,
th {
    border: 1px solid #ddd;
    padding: 8px;
}

td {
    color: #000000;

}

.btn-action {
    padding: 6px 12px;
    width: 100px;
    margin: 4px;
    border: none;
    border-radius: 4px;
    background-color: #007BFF;
    color: white;
    cursor: pointer;
    font-size: 14px;
}

.btn-action:hover {
    background-color: #0056b3;
}

.btn-delete {
    background-color: #FF4D4D;
}

.btn-delete:hover {
    background-color: #cc0000;
}

.th_c {
    display: flex;
    justify-content: center;
}

.td_button_c {
    text-align: center;

}

@media (max-width: 500px) {
    .td_button_c {
        display: flex;
        flex-direction: row;

    }
}


/*////////////////////style modal////////////////////*/
.modal_edit_cell {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: fixed;
    width: 100dvw;
    height: 100dvh;
    background-color: rgba(73, 111, 235, 0.404);
    padding: 20px;
    z-index: 1000;


}

.container_modal_edit_cell {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ffffff;
    width: 70dvw;
    border-radius: 7px;
}


#title_modal_edit_cell {
    text-align: center;
}

#label_edit_cell {
    text-align: center;
    margin-bottom: 30px;
    font-size: 20px;
}

#input_modal_edit_cell {
    height: 30px;
    border-radius: 6px;
    margin-left: 5px;
    padding-left: 5px;
}

.container_input_modal_edit_cell {
    display: flex;

}

.container_button_edit_cell {
    margin: 10px;

}

.bt_modal_cell {
    width: 90px;
    height: 30px;
    margin: 0px 10px;
}

/* button modal */
#bt_cancelar_editar_cell {
    background-color: #FF4D4D;
    color: #ffffff;
}

#bt_salvar_cell {
    background-color: #007BFF;
    color: #ffffff;
}

#bt_deletar_cell {
    background-color: #007BFF;
    color: #ffffff; 
}
#bt_cancelar_delete_cell{
    background-color: #FF4D4D;
    color: #ffffff;
}