
.container_prod_{
 display: flex;
 flex-direction: column;
 align-items: center;

 min-height: 100dvh;
 background-image: linear-gradient(0deg,#b4c2ce,#0c283a);

}
.title_prod_{
color: #ffffff;
}
.procucts_{
    width: 200px;
    min-height: 40px;
    border-radius: 7px;
    margin-bottom: 30px;
    margin-top: 10px;
}
.center_prod_{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 50dvh;
    justify-content: space-around;
}
.span_prod_{
    margin-bottom: 30px;
    color:#ee497b;
}
.button_prod_{
    width: 200px;
    background-color: #05598d;
    min-height: 40px;
    color: #ffffff;
    border-radius: 7px;
}