.containerQrcode{
display:flex;

align-items: center;
width: 100dvw;
min-height: 100dvh;
flex-direction: column;
background-image: linear-gradient(0deg,#b4c2ce,#0c283a);
}
#h2_GenerateQR{
    color: #ffffff;
}
#widthQRcode_GenerateQR{
    margin-left:10px;
    border-radius: 5px;
    width: 50px;
}
.inputGenerateQR{
height: 20px;
border-radius: 7px;
margin: 20px;
width: 300px;
padding: 7px;
outline:none;
border: 1px solid #b6b6b6;

}
.C_qrcode{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.button_GenerateQR{
 width: 200px;
 background-color: #05598d;
 min-height: 40px;
 color: #ffffff;
 border-radius: 7px;
}
.button_GenerateQR a {
    color: #ffffff;
    text-decoration: none;
}
.celula_qrcode{
    width: 200px;
    min-height: 40px;
    border-radius: 7px;
    margin-bottom: 30px;
    margin-top: 10px;
}