.conteiner_scann {
    display: flex ;
    align-items: center;
     justify-content: space-evenly; 
    flex-direction: column;
    height: 100dvh;
    background-image: linear-gradient(0deg, #b4c2ce, #0c283a);


}

.c_home_scann {
    width: 100%;
    background-image: linear-gradient(90deg, #2d3944, #0c283a);
}

.title_scann {
    font-size: 30px;

    color: #ffffff;

}
.button_scann {
    width: 50%;
    height: 45px;
    font-size: 18px;
    color: #ffffff;
    background-color: #0c283a;
    border-radius: 7px;
}

.container_menu_scann{
    display: flex;
    flex-wrap: wrap;
    height: 50%;
    width: 99dvw;
}
.button_menu_scann {
    flex-grow: 1;
    flex-basis: 150px;
    margin: 5px;
    height: 40%; 
    font-size: 18px;
    color: #ffffff;
    background-color: #0c283a;
    border-radius: 7px;
}
