.form{
display: flex;
flex-direction: column;
margin-top: 8.8rem;
}

.container {
 height: 100%;
  border-bottom: solid 3px #153042;
  margin-bottom: 3px;
  margin-top: 3px;
  
}

.title {
  display: flex;
  width: 100%;
  height:2rem ;
  text-align: start;
  align-items: center;
  /* background-color: #282c34; */
  background-color: #153042;
  color: aliceblue;
}
.title_label{
  padding-left: 10px;
}
.container_question{
  border-bottom: solid 2px ;
  
  
}
.question{
  font-size: 20px;
  padding: 5px;
}
.title_Result{
  
  text-align: center;
  font-size: 1rem;
  background-color: #d5d6d6b0;
  width: 100%;
padding-bottom: 10px;
}
.container_checkbox{
  display: flex;
  align-items: center;
  justify-content: space-around;
  background-color: #d5d6d6b0;
  height: 30px;
  
}
.title_checkbox{
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 30px;
  font-size: 25px;
}
.checkbox {
  width: 25px;
  height: 25px;
}
.text_Area{
padding: 10px;
background-color: #d5d6d6b0;

}
.textArea{
  resize: none;
  width: 100%;

}


.C_button{
  display: flex;
  width: 100%;
  height: 70px;
  justify-content: center;
}
#button{
  background-color: #153042;
  width: 200px;
  height: 40px;
  font-size: 18px;
  color: #ffffff;
  border-radius: 7px;
}
#button:active{
  background-color: #ffffff;
  color: #282c34;
}
/*   display none e diplay block dos checkbox */
.preview{
  display: flex;
  height: 30px;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  
}

@media screen and (min-width : 1020px) {
.form{
  width: 800px;
  margin-left: 10px;
}

 

 
}